/* Admin Page Layout */
.admin-container {
    display: flex;
    width: stretch;
    justify-content: center;
    background-color: #050505;
    padding-bottom: 60px;
    box-sizing: border-box;
    overflow: hidden;
}

.admin {
  display: flex;
  flex-direction: column;
    gap: 20px;
    width: 100%;
    max-width: 1500px;
    padding: 40px 80px;
    background-color: #050505;
    color: #F2F2F2;
}

.admin-title {
    font-family: "Proxima Nova";
    font-size: 64px;
    font-style: normal;
    font-weight: 700;
    line-height: 32px;
    letter-spacing: 6.4px;
    color: #F2F2F2;
}

.team-section {
  display: flex;
  flex-direction: row;
  gap: 20px;
  box-sizing: border-box;
}

.team-display, 
.edit-team-form {
  display: flex;
  flex-direction: column;
  flex: 1;
  gap: 20px;
  background-color: #0E0E0E;
  border-radius: 20px;
  padding: 20px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.5);
  max-height: fit-content;
}

.edit-team-title {
  color: #F2F2F2;
  font-family: "Proxima Nova";
  font-size: 32px;
  font-weight: 600;
  font-style: normal;
  text-transform: uppercase;
  line-height: 24px;
  letter-spacing: 4px;
  text-align: center;
}

.team-item {
  background-color: #1C1C1C;
  padding: 0px 20px 20px 20px;
  margin-bottom: 20px;
  border-radius: 16px;
  border: 1px solid #333;
  display: flex;
  flex-direction: column;
}

.team-item h4 {
  font-family: "Proxima Nova";
  font-size: 24px;
  font-style: normal;
  font-weight: 600;
  line-height: 24px;
  letter-spacing: 0px;
  color: #F2F2F2;
  margin: 0px;
  margin-bottom: 20px;
  text-transform: capitalize;
}

.edit-team-row {
  display: flex;
  gap: 20px;
}

.edit-team-form h2 {
  color: #F2F2F2;
  font-family: "Proxima Nova";
  font-size: 32px;
  font-weight: 600;
  font-style: normal;
  text-transform: uppercase;
  line-height: 24px;
  letter-spacing: 4px;
  text-align: center;
}

.edit-team-form-column {
  display: flex;
  flex-direction: column;
}

.edit-team-form-column label {
 margin-bottom: 10px; 
}

.edit-team-form-column input,
.edit-team-form-column textarea {
  background-color: #1C1C1C;
  color: #F2F2F2;
  font-family: "Proxima Nova";
  font-size: 16px;
  font-weight: 300;
  border: 1px solid #333;
  border-radius: 12px;
  padding: 15px;
  resize: vertical; 
  outline: none;
  transition: border-color 0.3s ease;
  margin-bottom: 20px;
  width: stretch;
}

.edit-team-form-column textarea {
  min-height: 50px;
  height: 100px;
}

.edit-team-form-column input:focus,
.edit-team-form-column textarea:focus {
  border-color: #9ECB3B;
} 

.announcements-section {
    display: flex;
    gap: 20px;
}
  
.current-announcements {
    color: #F2F2F2;
    font-family: "Proxima Nova";
    font-size: 32px;
    font-weight: 600;
    font-style: normal;
    text-transform: uppercase;
    line-height: 24px;
    letter-spacing: 4px;
    text-align: center;
    padding-bottom: 20px; 
}

  .announcement-item {
    background-color: #1C1C1C;
    padding: 0px 20px 20px 20px;
    margin-bottom: 20px;
    border-radius: 16px;
    border: 1px solid #333;
    display: flex;
    flex-direction: column;
}
  
  .announcement-item h3 {
    font-family: "Proxima Nova";
    font-size: 24px;
    font-style: normal;
    font-weight: 600;
    line-height: 24px;
    letter-spacing: 0px;
    color: #F2F2F2;
    margin-bottom: 0px;
    text-transform: capitalize;
  }
  
  .announcement-item p {
    font-size: 16px;
    font-family: "Proxima Nova";
    font-style: normal;
    font-weight: 300;
    line-height: 18px;
    color: #F2F2F2;
  }
  
  .admin-news-date {
    color: #F2F2F2;
    font-size: 16px;
    font-family: "Proxima Nova";
    font-style: normal;
    font-weight: 300;
    line-height: 16px;
    margin-top: 8px;
    margin-bottom: 8px;
  }

  .admin-actions {
    display: flex;
    gap: 15px;
    justify-content: flex-start;
    margin-top: 20px;
  }
  
  .admin-edit-button {
    padding: 15px 30px;
    font-family: "Proxima Nova";
    font-size: 20px;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 3.2px;
    border: none;
    border-radius: 20px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }

  .admin-delete-button {
    padding: 15px 30px;
    font-family: "Proxima Nova";
    font-size: 20px;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 3.2px;
    border: none;
    border-radius: 20px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .admin-edit-button:hover {
    background-color: #161616;
  }

  .admin-edit-button:active {
    background-color: #9ECB3B;
    color: #050505;
  }

  .admin-delete-button:hover {
    background-color: #161616;
  }
  
  .admin-delete-button:active {
    background-color: #EE375F;
  }
  
.announcements-section {
  display: flex;
  gap: 20px; 
}

.announcements-list {
  flex: 1.5; 
  background-color: #0E0E0E;
  border-radius: 20px;
  padding: 10px 20px 20px 20px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.5);
  max-height: fit-content;
}

.announcement-update-container {
    flex: 1.5;
    background-color: #0E0E0E; 
    border-radius: 20px; 
    padding: 20px; 
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.5); 
    display: flex;
    flex-direction: column;
    gap: 10px; 
}
  
  .announcement-update-title {
    color: #F2F2F2;
    font-family: "Proxima Nova";
    font-size: 32px;
    font-weight: 600;
    font-style: normal;
    text-transform: uppercase;
    line-height: 24px;
    letter-spacing: 4px;
    text-align: center;
  }
  
  .announcement-update-container input,
  .announcement-update-container textarea {
    background-color: #1C1C1C;
    color: #F2F2F2;
    font-family: "Proxima Nova";
    font-size: 16px;
    font-weight: 300;
    border: 1px solid #333;
    border-radius: 12px;
    padding: 15px;
    resize: vertical; 
    outline: none;
    transition: border-color 0.3s ease;
    margin-bottom: 20px;
    width: stretch;
  }

  .announcement-update-container textarea {
    min-height: 50px;
    height: 100px;
  }
  
  .announcement-update-container input:focus,
  .announcement-update-container textarea:focus {
    border-color: #9ECB3B;
  }
  
  .announcement-update-container button {
    padding: 20px 30px;
    font-family: "Proxima Nova";
    font-size: 20px;
    text-transform: uppercase;
    letter-spacing: 3.2px;
    border: none;
    border-radius: 20px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    margin-top: 20px;
  }
  
  .announcement-update-container button:hover {
    background-color: #161616;
  }
  
  .announcement-update-container button:active {
    background-color: #9ECB3B;
    color: #050505;
  }

.admin-schedule-section {
  display: flex;
  gap: 20px;
  margin-top: 40px;
}

.admin-schedule-list {
  flex: 1.5; 
  background-color: #0E0E0E;
  border-radius: 20px;
  padding: 20px; 
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.5);
  max-height: fit-content;
  gap: 20px;
}

.admin-current-schedule {
  color: #F2F2F2;
  font-family: "Proxima Nova";
  font-size: 32px;
  font-weight: 600;
  text-transform: uppercase;
  line-height: 24px;
  letter-spacing: 4px;
  text-align: center;
  padding-bottom: 20px;
}

.admin-schedule-item {
  background-color: #1C1C1C;
  padding: 0px 20px 20px 20px;
  margin-bottom: 20px;
  border-radius: 16px;
  border: 1px solid #333;
  display: flex;
  flex-direction: column;
}

.admin-schedule-item .row-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.admin-schedule-item .sport-title-wrapper {
  display: flex;
  align-items: center;
  gap: 16px;
}

.admin-schedule-item .sport-icon {
  width: 32px;
  height: 32px;
  display: block;
}

.admin-schedule-item .sport-title {
  color: #F2F2F2;
  font-family: "Proxima Nova";
  font-size: 20px;
  font-weight: 600;
  font-style: normal;
  text-transform: uppercase;
  line-height: 32px;
  letter-spacing: 4px;
  align-items: center;
  margin: 0;
}

.admin-schedule-date {
  color: #F2F2F2;
  font-family: "Proxima Nova";
  font-size: 20px;
  font-weight: 600;
  text-transform: uppercase;
  display: flex; 
  align-items: center;
  justify-content: flex-end;
  padding: 8px 16px;
  border-radius: 16px;
  background-color: #333;
  min-width: 120px; 
  text-align: center;
}

.admin-schedule-item h3 {
  font-size: 24px;
  font-family: "Proxima Nova";
  font-weight: 600;
  color: #F2F2F2;
  margin-bottom: 5px;
  text-transform: capitalize;
}

.admin-schedule-item p {
  font-size: 16px;
  font-family: "Proxima Nova";
  font-weight: 300;
  color: #F2F2F2;
}

.admin-schedule-item a {
  color: #9ECB3B;
  text-decoration: none;
  font-weight: 600;
  transition: color 0.3s ease;
}

.admin-schedule-item a:hover {
  color: #7AB32E;
}

.admin-schedule-item ul {
  list-style: none;
  padding: 0;
  margin-top: 10px;
}

.admin-schedule-item li {
  font-size: 16px;
  font-family: "Proxima Nova";
  font-weight: 300;
  color: #F2F2F2;
  margin-bottom: 5px;
}

/* Add Schedule Section */
.schedule-update-title {
  color: #F2F2F2;
  font-family: "Proxima Nova";
  font-size: 32px;
  font-weight: 600;
  font-style: normal;
  text-transform: uppercase;
  line-height: 24px;
  letter-spacing: 4px;
  text-align: center;
}

.add-schedule {
  flex: 1.5;
  background-color: #0E0E0E;
  border-radius: 20px;
  padding: 20px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.add-schedule label {
  color: #F2F2F2;
  font-size: 16px;
  font-family: "Proxima Nova";
  font-weight: 400;
  margin-bottom: 5px;
}

.add-schedule select,
.add-schedule input {
  background-color: #1C1C1C;
  color: #F2F2F2;
  font-family: "Proxima Nova";
  font-size: 16px;
  font-weight: 300;
  border: 1px solid #333;
  border-radius: 12px;
  padding: 15px;
  outline: none;
  transition: border-color 0.3s ease;
  margin-bottom: 10px;
  width: stretch;
}

.add-schedule select:focus,
.add-schedule input:focus {
  border-color: #9ECB3B;
}

.add-schedule button {
  padding: 20px 30px;
    font-family: "Proxima Nova";
    font-size: 20px;
    text-transform: uppercase;
    letter-spacing: 3.2px;
    border: none;
    border-radius: 20px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    margin-top: 20px;
}

.add-schedule button:hover {
  background-color: #161616;
}

.add-schedule button:active {
  background-color: #9ECB3B;
  color: #050505;
}

.meeting-time::-webkit-calendar-picker-indicator {
  filter: invert(1);
}
